import {
  Box,
  Checkbox,
  FormControlLabel,
  makeStyles,
  TextField,
  Typography,
} from '@material-ui/core';
import CheckBoxIcon from '@material-ui/icons/CheckBox';
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { Autocomplete } from '@material-ui/lab';
import React, { useCallback, useMemo } from 'react';


const useStyles = makeStyles({
  formControl: {
    maxWidth: 300,
  },
});







export const SelectedRelationsFilter = ({
  relationPairs,
  value,
  onChange,
}) => {
  const classes = useStyles();
  const relations = useMemo(() => relationPairs.flat(), [relationPairs]);

  const handleChange = useCallback(
    (_, v) => {
      onChange(relations.every(r => v.includes(r)) ? undefined : v);
    },
    [relations, onChange],
  );

  const handleEmpty = useCallback(() => {
    onChange(value?.length ? value : undefined);
  }, [value, onChange]);

  return (
    React.createElement(Box, { pb: 1, pt: 1,}
      , React.createElement(Typography, { variant: "button",}, "Relations")
      , React.createElement(Autocomplete, {
        className: classes.formControl,
        multiple: true,
        limitTags: 4,
        disableCloseOnSelect: true,
        'aria-label': "Relations",
        options: relations,
        value: value ?? relations,
        onChange: handleChange,
        onBlur: handleEmpty,
        renderOption: (option, { selected }) => (
          React.createElement(FormControlLabel, {
            control: 
              React.createElement(Checkbox, {
                icon: React.createElement(CheckBoxOutlineBlankIcon, { fontSize: "small",} ),
                checkedIcon: React.createElement(CheckBoxIcon, { fontSize: "small",} ),
                checked: selected,}
              )
            ,
            label: option,}
          )
        ),
        size: "small",
        popupIcon: React.createElement(ExpandMoreIcon, { 'data-testid': "selected-relations-expand",} ),
        renderInput: params => React.createElement(TextField, { ...params, variant: "outlined",} ),}
      )
    )
  );
};
