import { alertApiRef, useApi } from '@backstage/core-plugin-api';
import { catalogApiRef } from '@backstage/plugin-catalog-react';
import {
  Box,
  Checkbox,
  FormControlLabel,
  makeStyles,
  TextField,
  Typography,
} from '@material-ui/core';
import CheckBoxIcon from '@material-ui/icons/CheckBox';
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { Autocomplete } from '@material-ui/lab';
import React, { useCallback, useEffect, useMemo } from 'react';
import useAsync from 'react-use/lib/useAsync';

const useStyles = makeStyles({
  formControl: {
    maxWidth: 300,
  },
});






export const SelectedKindsFilter = ({ value, onChange }) => {
  const classes = useStyles();
  const alertApi = useApi(alertApiRef);
  const catalogApi = useApi(catalogApiRef);

  const { error, value: kinds } = useAsync(async () => {
    return await catalogApi
      .getEntityFacets({ facets: ['kind'] })
      .then(response => response.facets.kind?.map(f => f.value).sort() || []);
  });

  useEffect(() => {
    if (error) {
      alertApi.post({
        message: `Failed to load entity kinds`,
        severity: 'error',
      });
    }
  }, [error, alertApi]);

  const normalizedKinds = useMemo(
    () => (kinds ? kinds.map(k => k.toLocaleLowerCase('en-US')) : kinds),
    [kinds],
  );

  const handleChange = useCallback(
    (_, v) => {
      onChange(
        normalizedKinds && normalizedKinds.every(r => v.includes(r))
          ? undefined
          : v,
      );
    },
    [normalizedKinds, onChange],
  );

  const handleEmpty = useCallback(() => {
    onChange(value?.length ? value : undefined);
  }, [value, onChange]);

  if (!kinds?.length || !normalizedKinds?.length || error) {
    return React.createElement(React.Fragment, null);
  }

  return (
    React.createElement(Box, { pb: 1, pt: 1,}
      , React.createElement(Typography, { variant: "button",}, "Kinds")
      , React.createElement(Autocomplete, {
        className: classes.formControl,
        multiple: true,
        limitTags: 4,
        disableCloseOnSelect: true,
        'aria-label': "Kinds",
        options: normalizedKinds,
        value: value ?? normalizedKinds,
        getOptionLabel: k => kinds[normalizedKinds.indexOf(k)] ?? k,
        onChange: handleChange,
        onBlur: handleEmpty,
        renderOption: (option, { selected }) => (
          React.createElement(FormControlLabel, {
            control: 
              React.createElement(Checkbox, {
                icon: React.createElement(CheckBoxOutlineBlankIcon, { fontSize: "small",} ),
                checkedIcon: React.createElement(CheckBoxIcon, { fontSize: "small",} ),
                checked: selected,}
              )
            ,
            label: kinds[normalizedKinds.indexOf(option)] ?? option,}
          )
        ),
        size: "small",
        popupIcon: React.createElement(ExpandMoreIcon, { 'data-testid': "selected-kinds-expand",} ),
        renderInput: params => React.createElement(TextField, { ...params, variant: "outlined",} ),}
      )
    )
  );
};
