import { parseEntityRef } from '@backstage/catalog-model';
import {
  Content,
  ContentHeader,
  Header,
  Page,
  SupportButton,
} from '@backstage/core-components';
import { useAnalytics, useRouteRef } from '@backstage/core-plugin-api';
import {
  entityRouteRef,
  humanizeEntityRef,
} from '@backstage/plugin-catalog-react';
import { Grid, makeStyles, Paper, Typography } from '@material-ui/core';
import FilterListIcon from '@material-ui/icons/FilterList';
import ZoomOutMap from '@material-ui/icons/ZoomOutMap';
import { ToggleButton } from '@material-ui/lab';
import React, { useCallback } from 'react';
import { useNavigate } from 'react-router';
import {
  ALL_RELATION_PAIRS,


  EntityRelationsGraph,

} from '../EntityRelationsGraph';
import { DirectionFilter } from './DirectionFilter';
import { MaxDepthFilter } from './MaxDepthFilter';
import { SelectedKindsFilter } from './SelectedKindsFilter';
import { SelectedRelationsFilter } from './SelectedRelationsFilter';
import { SwitchFilter } from './SwitchFilter';
import { useCatalogGraphPage } from './useCatalogGraphPage';

const useStyles = makeStyles(theme => ({
  content: {
    minHeight: 0,
  },
  container: {
    height: '100%',
    maxHeight: '100%',
    minHeight: 0,
  },
  fullHeight: {
    maxHeight: '100%',
    display: 'flex',
    minHeight: 0,
  },
  graphWrapper: {
    position: 'relative',
    flex: 1,
    minHeight: 0,
    display: 'flex',
  },
  graph: {
    flex: 1,
    minHeight: 0,
  },
  legend: {
    position: 'absolute',
    bottom: 0,
    right: 0,
    padding: theme.spacing(1),
    '& .icon': {
      verticalAlign: 'bottom',
    },
  },
  filters: {
    display: 'grid',
    gridGap: theme.spacing(1),
    gridAutoRows: 'auto',
    [theme.breakpoints.up('lg')]: {
      display: 'block',
    },
    [theme.breakpoints.only('md')]: {
      gridTemplateColumns: 'repeat(3, 1fr)',
    },
    [theme.breakpoints.only('sm')]: {
      gridTemplateColumns: 'repeat(2, 1fr)',
    },
    [theme.breakpoints.down('xs')]: {
      gridTemplateColumns: 'repeat(1, 1fr)',
    },
  },
}));

export const CatalogGraphPage = (props











) => {
  const { relationPairs = ALL_RELATION_PAIRS, initialState } = props;

  const navigate = useNavigate();
  const classes = useStyles();
  const catalogEntityRoute = useRouteRef(entityRouteRef);
  const {
    maxDepth,
    setMaxDepth,
    selectedKinds,
    setSelectedKinds,
    selectedRelations,
    setSelectedRelations,
    unidirectional,
    setUnidirectional,
    mergeRelations,
    setMergeRelations,
    direction,
    setDirection,
    rootEntityNames,
    setRootEntityNames,
    showFilters,
    toggleShowFilters,
  } = useCatalogGraphPage({ initialState });
  const analytics = useAnalytics();
  const onNodeClick = useCallback(
    (node, event) => {
      const nodeEntityName = parseEntityRef(node.id);

      if (event.shiftKey) {
        const path = catalogEntityRoute({
          kind: nodeEntityName.kind.toLocaleLowerCase('en-US'),
          namespace: nodeEntityName.namespace.toLocaleLowerCase('en-US'),
          name: nodeEntityName.name,
        });

        analytics.captureEvent(
          'click',
          node.title ?? humanizeEntityRef(nodeEntityName),
          { attributes: { to: path } },
        );
        navigate(path);
      } else {
        analytics.captureEvent(
          'click',
          node.title ?? humanizeEntityRef(nodeEntityName),
        );
        setRootEntityNames([nodeEntityName]);
      }
    },
    [catalogEntityRoute, navigate, setRootEntityNames, analytics],
  );

  return (
    React.createElement(Page, { themeId: "home",}
      , React.createElement(Header, {
        title: "Catalog Graph" ,
        subtitle: rootEntityNames.map(e => humanizeEntityRef(e)).join(', '),}
      )
      , React.createElement(Content, { stretch: true, className: classes.content,}
        , React.createElement(ContentHeader, {
          titleComponent: 
            React.createElement(ToggleButton, {
              value: "show filters" ,
              selected: showFilters,
              onChange: () => toggleShowFilters(),}
            
              , React.createElement(FilterListIcon, null ), " Filters"
            )
          ,}
        
          , React.createElement(SupportButton, null, "Start tracking your component in by adding it to the software catalog."


          )
        )
        , React.createElement(Grid, { container: true, alignItems: "stretch", className: classes.container,}
          , showFilters && (
            React.createElement(Grid, { item: true, xs: 12, lg: 2, className: classes.filters,}
              , React.createElement(MaxDepthFilter, { value: maxDepth, onChange: setMaxDepth,} )
              , React.createElement(SelectedKindsFilter, {
                value: selectedKinds,
                onChange: setSelectedKinds,}
              )
              , React.createElement(SelectedRelationsFilter, {
                value: selectedRelations,
                onChange: setSelectedRelations,
                relationPairs: relationPairs,}
              )
              , React.createElement(DirectionFilter, { value: direction, onChange: setDirection,} )
              , React.createElement(SwitchFilter, {
                value: unidirectional,
                onChange: setUnidirectional,
                label: "Simplified",}
              )
              , React.createElement(SwitchFilter, {
                value: mergeRelations,
                onChange: setMergeRelations,
                label: "Merge Relations" ,}
              )
            )
          )
          , React.createElement(Grid, { item: true, xs: true, className: classes.fullHeight,}
            , React.createElement(Paper, { className: classes.graphWrapper,}
              , React.createElement(Typography, {
                variant: "caption",
                color: "textSecondary",
                display: "block",
                className: classes.legend,}
              
                , React.createElement(ZoomOutMap, { className: "icon",} ), " Use pinch & zoom to move around the diagram. Click to change active node, shift click to navigate to entity."


              )
              , React.createElement(EntityRelationsGraph, {
                rootEntityNames: rootEntityNames,
                maxDepth: maxDepth,
                kinds: 
                  selectedKinds && selectedKinds.length > 0
                    ? selectedKinds
                    : undefined
                ,
                relations: 
                  selectedRelations && selectedRelations.length > 0
                    ? selectedRelations
                    : undefined
                ,
                mergeRelations: mergeRelations,
                unidirectional: unidirectional,
                onNodeClick: onNodeClick,
                direction: direction,
                relationPairs: relationPairs,
                className: classes.graph,
                zoom: "enabled",}
              )
            )
          )
        )
      )
    )
  );
};
